import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { MovieDBService } from '../services/movie-dbservice.service';

@Component({
  selector: 'app-movie-detail',
  templateUrl: './movie-detail.component.html',
  styleUrls: ['./movie-detail.component.sass']
})
export class MovieDetailComponent implements OnInit {

  private id;
  public movie: any;
  public team: any;
  public background;

  constructor(private route: ActivatedRoute, private service: MovieDBService) { }

  ngOnInit() {
    this.route.params.subscribe(params => {
      this.id = +params['id'];
      this.load();
    });
  }

  private load() {
    this.service.detail(this.id)
      .subscribe(
      data => {
        if (data) {
          this.movie = data;
          this.background = 'https://image.tmdb.org/t/p/w1400_and_h450_bestv2/' + this.movie.backdrop_path;
        }
      }
      );

    this.service.team(this.id)
      .subscribe(
      data => {
        if (data) {
          this.team = data;
        }
      }
      );

  }

}



