import { Component, OnInit } from '@angular/core';
import { MovieDBService } from '../services/movie-dbservice.service';
import { ActivatedRoute } from '@angular/router';


@Component({
    selector: 'app-movie',
    templateUrl: './movie.component.html',
    styleUrls: ['./movie.component.sass']
})
export class MovieComponent implements OnInit {

    public page = 2;
    public movies: Array<any> = [];
    public categoryId: number;

    constructor(private service: MovieDBService, private route: ActivatedRoute) { }

    ngOnInit() {

        if (this.isDiscover()) {
            this.loadAplicacao();
        } else {
            this.route.params.subscribe(params => {
                this.categoryId = +params['id'];
                if (this.categoryId) {
                    this.loadGenres(this.categoryId);
                }
            });
        }
    }

    private loadAplicacao() {
        this.service.discover()
            .subscribe(
            data => {
                if (data) {
                    const response = data;
                    this.movies = response.results;
                }
            }
            );
    }

    onScroll() {

        this.service.infinityLoad(this.page, this.categoryId)
            .subscribe(
            data => {
                if (data) {
                    const response = data;
                    this.movies = this.movies.concat(response.results);
                    this.page++;
                }
            }
        );
    }

    loadGenres(categoryId) {
        this.service.genres(categoryId)
            .subscribe(
            data => {
                if (data) {
                    const response = data;
                    this.movies = response.results;
                }
            }
            );
    }

    private isDiscover() {
        const param = Number(document.URL.slice(document.URL.length - 1));
        if (param) {
            return false;
        }
        return true;
    }

}



